@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

* {
  font-family: "Roboto", sans-serif;
  line-height: 120%;
}

::-webkit-scrollbar {
  width: 7px;
}

::-webkit-scrollbar-track {
  background: #ffffff;
}

::-webkit-scrollbar-thumb {
  background: #D83939;
}

html {
  scroll-behavior: smooth;
}

body {
  display: flex;
  flex-direction: column;
  background: #F6F6F6;
}

main {
  flex: 1 1 auto;
}

.container {
  max-width: 1370px;
  margin: 0 auto;
  width: 100%;
  padding: 0 10px;
}

.button {
  display: block;
  background: #D83939;
  font-size: 15px;
  padding: 13px 0;
  color: #ffffff;
  transition: all ease 0.5s;
}
.button:hover {
  background: #d85151;
}

.title {
  font-weight: 700;
  color: #292B31;
  font-size: 32px;
}
.title_margin {
  margin-bottom: 28px;
}

.header {
  background: #ffffff;
  width: 100%;
  margin-bottom: 10px;
}
.header__top, .header__bottom {
  padding: 20px 0;
}
.header__bottom {
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.11);
  background: #ffffff;
}
.header__bottom .header__wrapper {
  justify-content: end;
}
.header__wrapper {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  gap: 20px;
  max-width: 1370px;
  margin: 0 auto;
  width: 100%;
  padding: 0 10px;
}
.header__search {
  max-width: 700px;
  width: 100%;
  display: flex;
}
.header__search input {
  width: 100%;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  height: 40px;
  line-height: 20px;
  padding: 0 10px;
  box-shadow: none;
  border: 1px solid #ccc;
  border-right: none;
}
.header__btn {
  position: relative;
  font-size: 0;
  white-space: nowrap;
}
.header__btn button {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  background: none;
  height: 40px;
  width: 40px;
  border: 1px solid #ccc;
}
.header__icons {
  max-width: 250px;
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
.header__icons button {
  font-family: "Roboto", sans-serif;
  font-size: 17px;
  color: #292B31;
  text-align: left;
  background: #ffffff;
  display: flex;
  align-items: center;
  gap: 10px;
}
.header__icons button:hover path {
  fill: #D83939;
}
.header__basket {
  position: relative;
}
.header svg path {
  transition: all ease 0.3s;
}
.header .basket__dropdown {
  z-index: 20;
  position: absolute;
  min-width: 300px;
  background: #ffffff;
  box-shadow: 0px 30px 60px rgba(0, 0, 0, 0.36);
  text-align: center;
  padding: 20px 0;
  bottom: -70px;
  right: 0;
  transform: scale(0);
  transform-origin: right;
  transition: all ease 0.5s;
}
.header .basket__dropdown_active {
  transform: scale(1);
}
.header__menu {
  width: 100%;
  transition: all ease 0.5s;
}
.header__menu ul {
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  gap: 20px;
}
.header__menu li:last-child a {
  background: #D83939;
  padding: 8px 20px;
  color: #ffffff;
}
.header__menu li:last-child a::after {
  content: unset;
}
.header__menu a {
  font-family: "Roboto", sans-serif;
  font-size: 17px;
  color: #292B31;
  position: relative;
}
.header__menu a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background: #292b31;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.5s;
}
.header__menu a:hover::after {
  transform: scaleX(1);
  transform-origin: left;
}
.header .burger-btn {
  display: none;
  z-index: 10;
  position: relative;
  width: 30px;
  height: 20px;
  cursor: pointer;
}
.header .burger-btn > span {
  position: absolute;
  background-color: #D83939;
  width: 100%;
  height: 3px;
  border-radius: 18px;
  left: 0;
  top: 9px;
  transition: all ease 0.5s;
}
.header .burger-btn:after, .header .burger-btn:before {
  content: "";
  position: absolute;
  background-color: #D83939;
  width: 100%;
  height: 3px;
  border-radius: 18px;
  left: 0;
  transition: all ease 0.5s;
}
.header .burger-btn:before {
  top: 20px;
}
.header .burger-btn:after {
  bottom: 20px;
}
.header .burger-btn_active {
  top: 20px;
  right: 20px;
  position: absolute;
}
.header .burger-btn_active span {
  opacity: 0;
  background-color: #ffffff;
}
.header .burger-btn_active:before {
  transform: rotate(45deg);
  top: 9px;
  background-color: #ffffff;
}
.header .burger-btn_active:after {
  transform: rotate(-45deg);
  bottom: 9px;
  background-color: #ffffff;
}
@media screen and (max-width: 768px) {
  .header__search, .header__sign {
    display: none !important;
  }
  .header__icons {
    justify-content: end;
  }
  .header .burger-btn {
    display: block;
  }
  .header__menu {
    position: absolute;
    max-width: none;
    flex-direction: column;
    gap: 30px;
    z-index: 2;
    right: -100%;
    top: 0;
    width: 80%;
    height: 100%;
    padding: 70px 50px;
    background: #D83939;
    transform: scaleX(0);
    transform-origin: right;
  }
  .header__menu ul {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .header__menu a {
    color: #ffffff;
  }
  .header__menu_active {
    transform: scaleX(1);
    right: 0;
  }
}
@media screen and (max-width: 400px) {
  .header__basket p {
    display: none;
  }
}

.popup-consultation {
  position: fixed;
  width: 100%;
  height: 100%;
  top: -300%;
  left: 0;
  z-index: 10;
  background: rgba(0, 0, 0, 0.6);
  transition: all ease 1s;
}
.popup-consultation__body {
  min-height: 100%;
  display: flex;
  align-items: center;
  margin: 0 auto;
  max-width: 460px;
  width: 100%;
}
.popup-consultation__content {
  position: relative;
  padding: 40px;
  width: 100%;
  background: #ffffff;
  border-radius: 5px;
}
.popup-consultation__top {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  width: 100%;
  margin-bottom: 30px;
}
.popup-consultation__title {
  font-family: #292B31;
  font-size: 26px;
  color: #292B31;
}
.popup-consultation__close {
  cursor: pointer;
}
.popup-consultation form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  width: 100%;
}
.popup-consultation .contact__input {
  flex-basis: 100%;
}
.popup-consultation__input {
  display: flex;
  flex-direction: column-reverse;
  position: relative;
  flex-basis: 100%;
  height: 65px;
  margin-bottom: 30px;
}
.popup-consultation input,
.popup-consultation textarea {
  width: 100%;
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  color: #292B31;
  padding: 10px 5px;
  border: 0;
  border: 1px solid #777E90;
  border-radius: 2px;
}
.popup-consultation textarea {
  height: 70%;
}
.popup-consultation label {
  position: absolute;
  left: 0;
  top: 0;
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  color: #292B31;
}
.popup-consultation .button {
  max-width: 320px;
  margin: 0 auto;
  font-family: "Roboto", sans-serif;
  font-size: 17px;
  font-weight: 500;
  color: #D83939;
  background: #ffffff;
  padding: 16px 36px;
  border: 1px solid #D83939;
  cursor: pointer;
  transition: all ease 0.5s;
}
.popup-consultation .button:hover {
  background: #D83939;
  color: #ffffff;
}
.popup-consultation p {
  width: 100%;
}
@media screen and (max-width: 768px) {
  .popup-consultation__body {
    height: 100%;
  }
  .popup-consultation__content {
    height: 100%;
    overflow: scroll;
  }
  .popup-consultation__input {
    flex-basis: 100%;
  }
}

.tabs {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
  min-height: 300px;
}
.tabs .t-btn-container {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 60px;
  align-items: center;
}
.tabs .t-btn-container .t-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.tabs .t-btn-container .t-btn.active {
  color: #D83939;
}
.tabs .t-panel-container .t-panel {
  transition: all 0.2s ease-in-out;
  opacity: 0;
  height: auto;
  max-height: 0px;
  overflow: hidden;
}
.tabs .t-panel-container .t-panel.active {
  opacity: 1;
  height: auto;
  max-height: 500px;
}

.breadcrumb {
  padding: 8px 15px;
  margin-bottom: 28px;
  display: flex;
}
.breadcrumb a {
  font-size: 12px;
  font-weight: 400;
  color: #292B31;
}
.breadcrumb strong {
  font-size: 12px;
  font-weight: 500;
  color: #D83939;
}

.cat-name {
  font-size: 18px;
}

.cat-checkbox {
  width: 25px;
}

.home-cat {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 10px;
  margin-bottom: 30px;
  /*&__wrapper{
    width: 200px;
  height: 200px;
  }
  &__img{
    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }*/
}
.home-cat__item {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  background: #ffffff;
  padding: 35px 20px 30px 40px;
  grid-column: span 1/span 1;
  transition: all ease 0.5s;
}
.home-cat__item img {
  width: 250px;
  height: 250px;
}
.home-cat__item:first-child {
  padding: 75px 120px;
  grid-column: span 2/span 2;
}
.home-cat__item:first-child .home-cat__description {
  width: 35%;
}
.home-cat__item:first-child .home-cat__title {
  font-size: 48px;
  margin-bottom: 40px;
}
.home-cat__item:first-child .home-cat__title:hover {
  color: #D83939;
}
.home-cat__item:first-child .home-cat__sub-title {
  font-size: 28px;
  text-transform: uppercase;
}
.home-cat__item:first-child .button {
  max-width: 217px;
  width: 100%;
}
.home-cat__item:first-child:hover .home-cat__title {
  color: #292B31;
}
.home-cat__item:hover {
  transform: scale(1.1);
  box-shadow: 0px 0px 100px -40px rgb(0, 0, 0);
}
.home-cat__item:hover .home-cat__title {
  color: #D83939;
}
.home-cat__description {
  width: 57%;
}
.home-cat__sub-title {
  font-size: 14px;
  margin-bottom: 30px;
  color: #292B31;
}
.home-cat__title {
  font-size: 26px;
  text-transform: uppercase;
  color: #292B31;
  margin-bottom: 10px;
  transition: all ease 0.5s;
}
.home-cat .button {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  max-width: 182px;
  width: 100%;
}
@media screen and (max-width: 1024px) {
  .home-cat__item {
    padding: 30px 10px;
  }
}
@media screen and (max-width: 768px) {
  .home-cat {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
  .home-cat__item {
    grid-column: span 2/span 2;
    flex-direction: column;
  }
  .home-cat__item:first-child {
    padding: 35px 20px 30px 40px;
    flex-direction: column-reverse;
  }
  .home-cat__item:first-child .home-cat__description {
    width: 100%;
  }
  .home-cat__item:first-child .home-cat__title {
    font-size: 30px;
  }
  .home-cat__description {
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .home-cat__img {
    margin-bottom: 30px;
  }
}

.home-info {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  gap: 10px;
  margin-bottom: 80px;
}
.home-info__item {
  display: flex;
  align-items: center;
  gap: 20px;
  background: #ffffff;
  padding: 25px 20px;
}
.home-info__description svg {
  flex-shrink: 0;
}
.home-info__title {
  font-weight: bold;
  font-size: 16px;
  color: #D83939;
}
.home-info__text {
  font-weight: 14;
  color: #292B31;
}
@media screen and (max-width: 1024px) {
  .home-info {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}
@media screen and (max-width: 768px) {
  .home-info {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
}

.home-product {
  width: 100%;
  position: relative;
}
.home-product .title {
  text-align: center;
}
.home-product .swiper-button-disabled svg {
  opacity: 0.25;
}
.home-product__prev, .home-product__next {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  position: absolute;
  top: 45%;
  width: 48px;
  height: 48px;
  background: #ffffff !important;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
  border-radius: 50%;
  z-index: 9;
  outline: none !important;
  border: none !important;
  cursor: pointer;
}
.home-product__prev svg, .home-product__next svg {
  transition: all ease 0.5s;
}
.home-product__prev svg path, .home-product__next svg path {
  transition: all ease 0.5s;
}
.home-product__prev:hover svg path, .home-product__next:hover svg path {
  fill: #D83939;
}
.home-product__prev {
  left: -25px;
}
.home-product__next {
  right: -25px;
}
.home-product__item {
  background-color: #ffffff;
  max-width: 340px;
  width: 100%;
  padding: 30px;
}
.home-product__img {
  max-width: 268px;
  width: 100%;
  max-height: 268px;
  margin-bottom: 43px;
}
.home-product__name {
  margin-bottom: 10px;
}
.home-product__name a {
  font-size: 16px;
  font-weight: 600;
  color: #292B31;
}
.home-product__table {
  width: 100%;
  margin-bottom: 20px;
}
.home-product__table table {
  width: 100%;
}
.home-product__table td {
  margin-bottom: 2px;
}
.home-product__table td:first-child, .home-product__table td:last-child {
  font-size: 14px;
  color: #777E90;
  white-space: nowrap;
  width: 50%;
  display: block;
  float: left;
  text-overflow: ellipsis;
  overflow: hidden;
}
.home-product__table td:last-child {
  color: #292B31;
}
.home-product__price-block {
  display: flex;
}
.home-product__price {
  display: flex;
  flex-flow: wrap;
  font-size: 18px;
  font-weight: 500;
  width: 60%;
  text-align: left;
  margin-bottom: 20px;
}
.home-product__price-old {
  color: #292B31;
  font-weight: 500;
  font-size: 14px;
  display: block;
  text-decoration: line-through;
  float: left;
  width: 100%;
  margin-bottom: 6px;
}
.home-product__price-new {
  font-weight: 600;
  font-size: 24px;
  color: #D83939;
  float: left;
}
.home-product__save {
  color: #292B31;
  flex-flow: wrap;
  font-size: 14px;
  text-align: right;
  font-weight: 500;
  width: 40%;
  padding-top: 3px;
}
.home-product__percent {
  width: 100%;
}
.home-product__buttons {
  display: flex;
  gap: 20px;
}
.home-product__button {
  border: 1px solid #292B31;
  color: #292B31;
  background: #ffffff;
  transition: all ease 0.5s;
}
.home-product__button:hover {
  background: #D83939;
  color: #ffffff;
  border-color: #D83939;
}
.home-product .button {
  max-width: 48%;
  height: 48px;
  width: 100%;
  padding: 0;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
@media screen and (max-width: 1400px) {
  .home-product__prev {
    left: -10px;
  }
  .home-product__next {
    right: -10px;
  }
}

.devices {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: row;
  padding: 28px;
  background: #ffffff;
}
.devices__item {
  width: 16.66666667%;
  margin-bottom: 40px;
}
.devices__item a {
  padding: 15px;
  text-align: center;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transition: all ease 0.5s;
}
.devices__item a:hover {
  box-shadow: 0px 10px 50px -20px rgb(0, 0, 0);
}
.devices__item a:hover .devices__name {
  color: #D83939;
}
.devices__img {
  width: 100%;
  max-width: 156px;
  max-height: 156px;
  object-fit: contain;
  margin-bottom: 10px;
}
.devices__name {
  display: block;
  font-size: 14px;
  line-height: 16px;
  font-weight: 500;
  color: #292B31;
  transition: all ease 0.5s;
}
@media screen and (max-width: 1200px) {
  .devices__item {
    width: 33.33333333%;
  }
}
@media screen and (max-width: 600px) {
  .devices__item {
    width: 50%;
  }
}
@media screen and (max-width: 420px) {
  .devices__item {
    width: 100%;
  }
}

.contact {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  width: 100%;
  padding: 40px;
  background: #ffffff;
}
.contact__title {
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 15px;
  color: #292B31;
}
.contact__text {
  font-size: 14px;
  margin-bottom: 15px;
  color: #292B31;
  font-weight: 400;
}
.contact span a {
  color: #2980b9;
}

.contact-form {
  margin-top: 15px;
  background-color: #ffffff;
}
.contact-form__container {
  max-width: 1370px;
  margin: 0 auto;
  width: 100%;
  padding: 40px;
}
.contact-form__header {
  font-size: 20px;
  font-weight: 100;
}
.contact-form__text {
  margin-top: 12px;
  font-size: 14px;
  font-weight: 100;
}
.contact-form__input {
  border: 1px solid #777e90;
  border-radius: 2px;
  font-size: 12px;
  color: #292b31;
  padding: 10px 5px;
  width: 350px;
  margin-bottom: 15px;
  color: #777E90;
  background-color: #ffffff;
  cursor: pointer;
  resize: none;
}
.contact-form__input:last-child {
  background-color: #D83939;
  border: 1px solid #ffffff;
  color: #ffffff;
  transition: all ease 0.5s;
}
.contact-form__input:last-child:hover {
  background-color: #ffffff;
  border: 1px solid #D83939;
  color: #D83939;
}

.catalog {
